/**
 * ACCOUNT
 * 
 * @file        src/pages/account.jsx
 */

/**
 * Dependencies.
 */
import React, { useContext } from 'react'
import { Link, navigate } from 'gatsby'
import { Router } from "@reach/router"
import PrivateRoute from '../components/privateRoute'
import Profile from '../components/account/profile.fr'
import Login from '../components/account/login.fr'
import { StateContext } from '../.gatsby/GlobalContextProvider'
import config from '../../gatsby-config'

/**
 * Page Meta.
 */
const pageMeta = {
  title: 'Compte',
  description: 'Compte d\'utilisateur',
}
 
const AccountLinks = ({pageContext}) => {
  return (<><Link to="/fr/account/profile">Profil</Link> / <Link to="/fr/account/logout">Logout</Link></>)
}

/**
 * Page (React component - JSX).
 */
const Account = () => {

    const context = useContext(StateContext)

    // Skip if authentication disabled.
    if (!config.siteMetadata.authentication.enable) {
      if( typeof window !== "undefined" ) navigate(-1)
      return null
    }

    // Account page.
    return (
      <>
      <div id="content" className="container col">
      <div className="content">
        <h1>{pageMeta.title}</h1>
        {context.authenticated ? (
        <>
        <p>Bienvenue dans votre compte {context.user.displayName}.</p>
        <p><AccountLinks/></p>
        </>
        )
        :
        <p>En utilisant notre site web, vous acceptez notre <Link to="/fr/confidentialite/">politique de confidentialité</Link> et nos <Link to="/fr/termes-et-conditions/">Termes et conditions</Link>.</p>
        }
        <Router>
          <PrivateRoute exact path="/fr/account/profile" component={Profile} />
          <Login path="/fr/account/login" />
        </Router>
      </div>
    </div>
      </>
    )

}

Account.pageMeta = pageMeta

export default Account